import { api } from "./api";

export interface NotificationProps {
  id: string;
  payload: string;
  isRead: boolean;
  createdAt: string;
  updatedAt: string;
  event: string;
  content: string;
}

const notificationService = {
  getNotification: async (): Promise<NotificationProps[]> => {
    try {
      const response = await api.get<NotificationProps[], Record<string, unknown>>(
        "/notifications",
      );
      return response;
    } catch (error) {
      console.error("ì•Œë¦¼ get ì‹¤íŒ¨", error);
      throw error;
    }
  },
  readNotification: async (notificationId: string) => {
    try {
      const response = await api.patch<NotificationProps, Record<string, unknown>>(
        `/notifications/${notificationId}`,
        {
          isRead: true,
        },
      );
      return response;
    } catch (error) {
      console.error("ì•Œë¦¼ patch ì‹¤íŒ¨", error);
      throw error;
    }
  },
};

export default notificationService;
